export const convertStringToDouble = (val: string): number =>
  Number.parseFloat(val.replace(/,/g, "."));

export const convertNumberToString = (val: number): string =>
  String(val).replace(/\./g, ",");

export const toggleNumberSign = (val: string): string =>
  /-/g.test(val) ? val.replace("-", "") : `-${val}`;

export const hasDecimalSeperator = (val: string): boolean => /,/g.test(val);
