import React, { useEffect, useState } from "react";
import {
  View,
  StyleSheet,
  Text,
  TouchableOpacity,
  ActivityIndicator,
} from "react-native";
import { Info } from "../../components/Info";
import {
  convertNumberToString,
  toggleNumberSign,
  hasDecimalSeperator,
} from "../../helpers";

interface CalculatorButtonProps {
  text: string;
  onClick: (text: string) => void;
  style?: any;
  disabled?: boolean;
  loading?: boolean;
}

const CalculatorButton: React.FC<CalculatorButtonProps> = ({
  text,
  onClick,
  disabled,
  loading = false,
  style,
}) => {
  const handleClick = () => {
    onClick(text);
  };
  return (
    <TouchableOpacity
      style={[styles.button].concat(style)}
      onPress={handleClick}
      disabled={disabled || loading}
    >
      <View style={styles.buttonContent}>
        <Text style={styles.buttonText}>{text}</Text>
        {loading && (
          <ActivityIndicator
            animating={true}
            style={{
              marginLeft: 8,
            }}
          />
        )}
      </View>
    </TouchableOpacity>
  );
};

interface CalculatorProps {
  calculate: (operand1: string, operand2: string, operator: string) => void;
  loading: boolean;
  result: string;
  error?: string;
  disabled?: boolean;
}

export const Calculator: React.FC<CalculatorProps> = ({
  calculate,
  loading,
  result,
  error,
  disabled = false,
}) => {
  const [state, setState] = useState({
    operand1: "",
    operand2: "",
    operator: "",
    result: result ?? "",
  });

  useEffect(() => {
    setState((s) => ({ ...s, result }));
  }, [result]);

  const handlePress = (value: string) => {
    const operand = !state.operator ? "operand1" : "operand2";

    if (value === "AC") {
      setState({
        operand1: "",
        operand2: "",
        operator: "",
        result: "",
      });
    } else if (value === "+/-" && (state.operand1 || state.operand2)) {
      setState((current) => {
        if (current.operand2) {
          return {
            ...current,
            operand2: toggleNumberSign(current.operand2),
          };
        } else {
          return {
            ...current,
            operand1: toggleNumberSign(current.operand1),
          };
        }
      });
    } else if (/[\d,]/g.test(value)) {
      if (hasDecimalSeperator(value) && hasDecimalSeperator(state[operand])) {
        return;
      }

      if (value.toString() === "0" && state[operand] === "0") {
        return;
      }

      if (
        value.toString() !== "0" &&
        !hasDecimalSeperator(value) &&
        state[operand] === "0"
      ) {
        setState((current) => ({
          ...current,
          [operand]: value.toString(),
        }));
        return;
      }

      if (state.result && hasDecimalSeperator(value)) {
        setState(() => ({
          operand1: "0,",
          operand2: "",
          operator: "",
          result: "",
        }));

        return;
      }

      if (!state[operand] && hasDecimalSeperator(value)) {
        setState((current) => ({
          ...current,
          [operand]: `0,`,
        }));

        return;
      }

      setState((current) =>
        current.result
          ? {
              operand1: value,
              operand2: "",
              operator: "",
              result: "",
            }
          : {
              ...current,
              [operand]: state[operand].concat(value),
            },
      );
    } else if (/[/x\-*+]/.test(value)) {
      if (!state.operand1) {
        return;
      }

      if (state.result === 0 || state.result) {
        setState((current) => ({
          operand1: convertNumberToString(current.result as unknown as number),
          operand2: "",
          operator: value,
          result: "",
        }));
        return;
      }

      setState((current) => ({
        ...current,
        operator: value,
      }));
    } else if (value === "=") {
      if (!loading && state.operand1 && state.operand2 && state.operator) {
        calculate(state.operand1, state.operand2, state.operator);
      }
    }
  };

  return (
    <View style={styles.container}>
      {Boolean(error) && <Info text={error} />}
      <View style={styles.screen}>
        <Text style={styles.screenText}>
          {state.operand1} {state.operator} {state.operand2}
          &nbsp;
          {state.result || state.result === 0 ? (
            <Text style={styles.resultText}>
              = {state.result.toString().replace(/\./g, ",")}
            </Text>
          ) : null}
        </Text>
      </View>
      <View style={styles.buttonGroup}>
        <View style={styles.row}>
          <CalculatorButton
            text="AC"
            onClick={handlePress}
            disabled={disabled}
            style={{
              backgroundColor: "#606060",
            }}
          />
          <CalculatorButton
            text="+/-"
            onClick={handlePress}
            disabled={disabled}
            style={{
              backgroundColor: "#606060",
            }}
          />
          <CalculatorButton
            text=""
            onClick={handlePress}
            disabled={true}
            style={{
              backgroundColor: "#606060",
            }}
          />
          <CalculatorButton
            text="/"
            onClick={handlePress}
            disabled={disabled}
            style={{
              backgroundColor: "#ff9500",
            }}
          />
        </View>
        <View style={styles.row}>
          <CalculatorButton
            text="7"
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton
            text="8"
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton
            text="9"
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton
            text="*"
            onClick={handlePress}
            disabled={disabled}
            style={{
              backgroundColor: "#ff9500",
            }}
          />
        </View>
        <View style={styles.row}>
          <CalculatorButton
            text="4"
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton
            text="5"
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton
            text="6"
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton
            text="-"
            onClick={handlePress}
            disabled={disabled}
            style={{
              backgroundColor: "#ff9500",
            }}
          />
        </View>
        <View style={styles.row}>
          <CalculatorButton
            text="1"
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton
            text="2"
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton
            text="3"
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton
            text="+"
            onClick={handlePress}
            disabled={disabled}
            style={{
              backgroundColor: "#ff9500",
            }}
          />
        </View>
        <View style={styles.row}>
          <CalculatorButton
            text="0"
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton text="" onClick={handlePress} disabled={true} />
          <CalculatorButton
            text=","
            onClick={handlePress}
            disabled={disabled}
          />
          <CalculatorButton
            text="="
            onClick={handlePress}
            loading={loading}
            style={{
              backgroundColor: "#ff9500",
            }}
          />
        </View>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    width: "100%",
    marginTop: 24,
    maxWidth: 500,
    height: 330,
  },
  row: {
    display: "flex",
    flex: 4,
    height: 48,
    width: "100%",
    flexDirection: "row",
  },
  buttonGroup: {
    width: "100%",
    height: 5 * 48 + 24,
  },
  button: {
    flex: 1,
    margin: 4,
    height: 48,
    alignItems: "center",
    justifyContent: "center",
    textTransform: "uppercase",
    width: "100%",
    backgroundColor: "#808080",
    borderRadius: 4,
  },
  buttonContent: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "center",
  },
  buttonText: {
    color: "#ffffff",
    fontWeight: "bold",
    fontSize: 20,
  },
  screen: {
    padding: 8,
    justifyContent: "center",
    backgroundColor: "#d4d4d2",
    marginBottom: 16,
    minHeight: 80,
    borderRadius: 4,
  },
  screenText: {
    fontWeight: "bold",
    fontSize: 24,
    textAlign: "right",
  },
  resultText: {
    fontWeight: "bold",
    fontSize: 28,
    textAlign: "right",
  },
});
